html, body {
  height: 100%;
  margin: 0;
  scroll-behavior: smooth;
}

.app-container {
  display: flex; /* Ensures elements inside it are arranged horizontally */
  height: 100%; /* Fill the whole viewport */
}
.page-wrapper {
  margin-left: 250px;
  flex-grow: 1;
  overflow: auto;
  transition: margin-left 0.3s ease;
}

/* Add this media query to reset on small screens */
@media (max-width: 768px) {
  .page-wrapper {
    margin-left: 0;
  }
}